/* 详情页样式 */
.detail-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  overflow: hidden;
}

.detail-header {
  padding: 40px 60px;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 30px;
}

.back-btn {
  padding: 12px 30px;
  font-size: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  color: #333;
  border: none;
  border-radius: 25px;
  transition: all 0.3s ease;
  font-weight: bold;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}

.back-btn:hover {
  background-color: white;
  transform: scale(1.05);
}

.back-btn:active {
  transform: scale(0.98);
}

.detail-header h1 {
  font-size: 48px;
  color: white;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.detail-header p {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
}

.detail-content {
  flex: 1;
  padding: 60px;
  color: white;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.detail-content p {
  font-size: 24px;
  line-height: 1.8;
}